import { auth, getTimelineCollection } from '@/firebase.config'
import { BaseContentCard } from '@/types/BaseContentCard';
import { useToaster } from '@ajmey/toaster/toaster'

const toaster = useToaster({animation: "appear", theme: "default", dismiss: 2000 })

const updateCardValue = async (docUid: string, field: keyof BaseContentCard, newValue: string): Promise<void> => {
    const { currentUser } = auth
    const { uid } = currentUser || {}
    if (!uid) return
    const docRef = getTimelineCollection(uid).doc(docUid)
    await docRef.update(field, newValue)
    toaster.success("Content updated ✔️")
}

export default updateCardValue
