<template>
  <div v-bind:class="`${isCardPreview ? 'card col-6 col-sm-3 m-0 p-0' : 'card col-12 m-0 p-0'} ${editMode ? '' : 'border-0' }`">

    <!-- PRIVATE OPTIONS -->       
    <div v-if="editMode" class="card-header p-0 m-0 bg-secondary text-center">
      
        <i class="fa fa-trash text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="onDelete"/>
        <i class="fa fa-refresh text-white mx-2 mx-sm-3" style="cursor: pointer;" v-if="hasMorePreviews" @click="changePreview"/>
        <i class="fa fa-external-link text-white mx-2 mx-sm-3" style="cursor: pointer;" v-if="isCardPreview" @click="toPost"/>
      
    </div>

    <!-- IFRAME -->
    <iframe v-bind:class="isCardPreview ? 'card-content':'post-content'" v-if="contentCard.type === 'iframe'" :src="contentCard.iFrameUrl"
      allowtransparency="true"
      frameborder="0"
      allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen
    />

    <!-- IMAGE -->
    <img v-bind:class="isCardPreview ? 'card-content':'post-content'" v-if="contentCard.type === 'image'" :src="contentCard.previewImage" style="cursor: pointer;" @click="toPost" />

    <!-- VIDEO -->
    <video v-bind:class="isCardPreview ? 'card-content':'post-content'" v-if="contentCard.type === 'video'" :src="contentCard.previewVideo" controls />
  
    <!-- PUBLIC OPTIONS -->       
    <div v-if="showPublicOptions" class="card-footer p-0 m-0 bg-secondary text-right">
      <i class="fa fa-external-link text-white mr-1" style="cursor: pointer;" v-if="isCardPreview" @click="toPost"/>
    </div>
  </div> 
</template>

<script lang="ts">
import { ref } from 'vue'
import { BaseContentCard } from '@/types/BaseContentCard'
import updateCardValue from '@/services/updateCardValue'
import router from '@/router'

export default {
  props: {
    editMode: Boolean,
    userUid: String,
    postUid: String,
    contentCard: BaseContentCard,
    onDelete: Function,
    isCardPreview: {
      type: Boolean,
      default: true
    },
    showPublicOptions: {
      type: Boolean,
      default: false
    },
  },
  setup (props) {
    const contentOptions = [
      ...props.contentCard.rawImages?.map(img => ({
        type: "image",
        previewImage: img
      })) || [],
      ...props.contentCard.rawVideos?.map(vid => ({
        type: "video",
        previewVideo: vid
      })) || [],
    ];
    if (props.contentCard.iFrameUrl) {
      contentOptions.push({
        type: 'iframe',
      })
    }

    let optIndex = 0;
    const hasMorePreviews = ref(contentOptions.length > 1);
    const changePreview = async () => {
      optIndex += 1;
      if (optIndex > contentOptions.length - 1) {
        optIndex = 0;
      }
      const { type, previewImage, previewVideo } = contentOptions[optIndex];
      if (props.contentCard.type !== type) {
        await Promise.all([
          updateCardValue(props.postUid, "type", type),
          updateCardValue(
            props.postUid,
            type === "image" ? "previewImage" : "previewVideo",
            type === "image" ? previewImage : previewVideo
          )
        ]);
      } else {
        await updateCardValue(
            props.postUid,
            type === "image" ? "previewImage" : "previewVideo",
            type === "image" ? previewImage : previewVideo
        )
      }
    };

    const toPost = () => router.replace(`/u/${props.userUid}/p/${props.postUid}`)

    return {
      changePreview,
      hasMorePreviews,
      toPost
    };
  }
}
</script>


<style lang="css">
/* Custom CSS here */

.card-content {
  height: 200px;
  /* width: 200px; */
  object-fit: cover;
}
</style>

